<template>
  <div class="container-sm">
    <pre>
      
      rsync
      thumbnail
      
    </pre>
    <div
      v-on:click="getFiles"
      class="btn btn-light">
      
    </div>
    <div
      v-for="(files_row,index) in files_split_row"
      :key="index + files_row"
      class="row no-gutters">
      <div
        v-for="(file, i) in files_row"
        :key="index + i + file"
        class="col-sm">
        <div
          class="text-center"
          >{{ file }}</div>
        <img
          v-if="file != ''"
          class="img-fluid"
          :src="image_path(file)"
          alt="" />
      </div>
    </div>
    <Uploader />
  </div>
</template>

<script>
import Uploader from '@/components/tako/Uploader.vue'
import _ from 'lodash'
import { mapState } from 'vuex'
import Axios from 'axios'

export default {
  components: {
    Uploader
  },
  data: function(){
    return {
      columns: 3,
      files: ['']
    }
  },
  computed: {
    ...mapState('constants', {
      apiUrl: state => state.apiUrl,
    }),
    files_split_row: function() {
      const chunked = _.chunk(this.files, this.columns)
      const lack = this.columns - chunked[chunked.length-1].length 
      for (var i = 0; i < lack; i++) {
        chunked[chunked.length-1].push('')
      }
      return chunked
    }
  },
  methods: {
    getFiles: function() {
      Axios.get(`${this.apiUrl}/tako/list`)
        .then(function(res){
          this.files = res.data
        }.bind(this))
    },
    image_path: function(filename){
      return [this.apiUrl,'tako','data','get',filename].join('/')
    }
  }
}
</script>
